* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.con {
  max-width: 1400px;
  width: calc(100% - 20px);
  margin: auto;
  border: 1px solid lightgrey;
  background-color: aliceblue;
}

header {
  background-color: lightblue;
  padding: 20px 10px;
  font-weight: bold;
  text-align: center;
}

.list {
  padding: 20px 10px;
  border-bottom: 1px solid lightgray;
  display: flex;
  flex-wrap: wrap;
  transition: 0.3s;
}

.listname {
  width: calc(100% - 100px);
  color: deepskyblue;
  font-weight: bold;
  font-size: 2rem;
}

.good {
  width: 100px;
  position: relative;
}

.good::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 3px;
  bottom: 20px;
  right: 60%;
  background-color: deepskyblue;
}

.good>span {
  cursor: pointer;
}

.date {
  width: 100%;
  font-size: 0.75rem;
  padding-top: 10px;
}

.input,
.date_input {
  display: flex;
  justify-content: center;
  align-items: center;
}

.input > input,
.input > button,
.date_input {
  height: 40px;
}

.input > button,
.list > button {
  width: 50px;
  border-color: aliceblue;
  background-color: aliceblue;
  cursor: pointer;
  transition: 0.3s;
}

.input > button:hover,
.list > button:hover {
  background-color: dodgerblue;
  border-color: dodgerblue;
  color: white;
  font-weight: bold;
}

.modal {
  background-color: #eee;
  padding: 20px;
  position: absolute;
  top: 100px;
  right: 0;
  width: 50%;
}
